import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { ModalController } from '@ionic/angular';
import { ProductService } from 'src/app/services/product.service';

@Component({
  selector: 'app-reservar',
  templateUrl: './reservar.component.html',
  styleUrls: ['./reservar.component.scss'],
})
export class ReservarComponent implements OnInit {

  constructor(
    private _product: ProductService,
    public formBuilder: FormBuilder,
    private _modal: ModalController) { }

  product: any;
  loginForm: FormGroup;
  ingresarUsuario = false;

  ngOnInit() {
    this.initForm();
  }

  initForm() {
    this.loginForm = this.formBuilder.group({
      name: ['']
    });
  }

  closeModal() {
    this._modal.dismiss({ updateList: this.reservado });
  }

  continuar() {
    this._modal.dismiss({
      updateList: this.reservado
    });
  }


  isVarInvalid(data: any) {
    if (typeof data === 'string' || data instanceof String) data = data.trim();
    if (data == undefined || data == null || data == 'undefined' || data == 'null' || data == '') return true;
    return false;
  }

  reservado = false;
  async reservar() {

    if (this.loginForm.controls['name'].value == '') {
      this.ingresarUsuario = true;
      return;
    }


    this.product.status = 2;
    this.product.tmp_reserva = new Date().getTime();
    this.product.usuario = this.loginForm.controls['name'].value;
    const res: any = await this._product.save(this.product, this.product._id)
    this.reservado = true;

  }

}
